import {
	Shape,
	Geometry,
	PathFigure,
	PathSegment,
	Spot,
} from 'gojs';

import createStar from './create-star';

Shape.defineFigureGenerator('Star', (shape, w, h) => {
	const starPoints = createStar(5);
	const geo = new Geometry();
	const fig = new PathFigure(starPoints[0].x * w, starPoints[0].y * h, true);
	geo.add(fig);

	for (let i = 1; i < 10; i++)
		fig.add(new PathSegment(PathSegment.Line, starPoints[i].x * w, starPoints[i].y * h));

	fig.add(new PathSegment(PathSegment.Line, starPoints[0].x * w, starPoints[0].y * h).close());

	geo.spot1 = new Spot(0.266, 0.333);
	geo.spot2 = new Spot(0.733, 0.733);

	return geo;
});
