import {
	Shape,
	Geometry,
	PathFigure,
	PathSegment,
	Spot,
} from 'gojs';

import createPolygon from './create-polygon';

Shape.defineFigureGenerator('Hexagon', (shape, w, h) => {
	const points = createPolygon(6);
	const geo = new Geometry();
	const fig = new PathFigure(points[0].x * w, points[0].y * h, true);
	geo.add(fig);

	for (let i = 1; i < 6; i++)
		fig.add(new PathSegment(PathSegment.Line, points[i].x * w, points[i].y * h));

	fig.add(new PathSegment(PathSegment.Line, points[0].x * w, points[0].y * h).close());

	geo.spot1 = new Spot(0.07, 0.25);
	geo.spot2 = new Spot(0.93, 0.75);

	return geo;
});
