export default (p1x, p1y, p2x, p2y, q1x, q1y, q2x, q2y, result) => {
	const dx1 = p1x - p2x;
	const dx2 = q1x - q2x;
	let x;
	let y;

	if (dx1 === 0 || dx2 === 0)
		if (dx1 === 0) {
			const m2 = (q1y - q2y) / dx2;
			const b2 = q1y - m2 * q1x;
			x = p1x;
			y = m2 * x + b2;
		} else {
			const m1 = (p1y - p2y) / dx1;
			const b1 = p1y - m1 * p1x;
			x = q1x;
			y = m1 * x + b1;
		}
	else {
		const m1 = (p1y - p2y) / dx1;
		const m2 = (q1y - q2y) / dx2;
		const b1 = p1y - m1 * p1x;
		const b2 = q1y - m2 * q1x;

		x = (b2 - b1) / (m1 - m2);
		y = m1 * x + b1;
	}

	result.x = x;
	result.y = y;
	return result;
};
