import {
	Point,
} from 'gojs';

import createPolygon from './create-polygon';
import getIntersection from './get-intersection';

export default (pointCount = 5) => {
	// First, create a regular polygon
	const polygon = createPolygon(pointCount);
	// Calculate the points inbetween
	const pts = []; // new Point[points * 2 + 1];

	const half = Math.floor(polygon.length / 2);
	const count = polygon.length - 1;
	const offset = (pointCount % 2 === 0) ? 2 : 1;

	for (let i = 0; i < count; i++) {
		// Get the intersection of two lines
		const p0 = polygon[i];
		const p1 = polygon[i + 1];
		const q21 = polygon[(half + i - 1) % count];
		const q2off = polygon[(half + i + offset) % count];

		pts[i * 2] = p0;
		pts[i * 2 + 1] = getIntersection(p0.x, p0.y,
			q21.x, q21.y,
			p1.x, p1.y,
			q2off.x, q2off.y, new Point()
		);
	}

	pts[pts.length] = pts[0];

	return pts;
};
