import {
	Shape,
	Geometry,
	PathFigure,
	PathSegment,
	Spot,
} from 'gojs';

Shape.defineFigureGenerator('RoundedTopRectangle', (shape, w, h) => {
	// this figure takes one parameter, the size of the corner
	let p1 = 5; // default corner size
	if (shape !== null) {
		const param1 = shape.parameter1;
		if (!Number.isNaN(param1) && param1 >= 0)
			p1 = param1; // can't be negative or NaN
	}

	p1 = Math.min(p1, w / 2);
	p1 = Math.min(p1, h / 2); // limit by whole height or by half height?

	const geo = new Geometry();
	// a single figure consisting of straight lines and quarter-circle arcs
	geo.add(new PathFigure(0, p1)
		.add(new PathSegment(PathSegment.Arc, 180, 90, p1, p1, p1, p1))
		.add(new PathSegment(PathSegment.Line, w - p1, 0))
		.add(new PathSegment(PathSegment.Arc, 270, 90, w - p1, p1, p1, p1))
		.add(new PathSegment(PathSegment.Line, w, h))
		.add(new PathSegment(PathSegment.Line, 0, h).close()));

	// don't intersect with two top corners when used in an "Auto" Panel
	geo.spot1 = new Spot(0, 0, 0.3 * p1, 0.3 * p1);
	geo.spot2 = new Spot(1, 1, -0.3 * p1, 0);

	return geo;
});
