import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

// Styles
import styles from './styles.scss';

// View
import view from './view';

// State
import state from './state';

// Action handlers
import actionHandlers from './actions';

// Diagram Events
import { eventTypes } from './constants';
const {
	LAYOUT_STARTED,
	LAYOUT_DONE,

	VIEWPORT_BOUNDS_CHANGED,

	FOCUSED,
	CLICKED,
	DRAGGING,
} = eventTypes;

// Custom element
export default createCustomElement('now-diagram', {
	renderer: {
		type: snabbdom,
		view,
	},
	styles,
	properties: {
		model: {},
		modelOptions: {},
		options: {},
		setTemplates: {},
		name: {},
		wcag: {
			default: true,
		},
		wcagNodesOrder: {},
	},
	actionHandlers,
	initialState: state.initial,
	transformState: state.transform,
	dispatches: {
		/**
		 * Dispatched when the diagram starts rendering.
		 * @type {{event: go.Event, diagram: go.Diagram}}
		 * @uib.description Dispatched when the diagram finished rendering
		 */
		[LAYOUT_STARTED]: {},

		/**
		 * Dispatched when the diagram finished rendering.
		 * @type {{event: go.Event, diagram: go.Diagram}}
		 * @uib.description Dispatched when the diagram finished rendering
		 */
		[LAYOUT_DONE]: {},

		/**
		 * Dispatched when the viewport changes its bounds (zoom, move the diagram, resize etc).
		 * @type {{event: go.Event, diagram: go.Diagram}}
		 * @uib.description Dispatched when the viewport changes its bounds (zoom, move the diagram, resize etc)
		 */
		[VIEWPORT_BOUNDS_CHANGED]: {},

		/**
		 * Dispatched when the user focused on the diagram.
		 * @type {{event: go.Event, diagram: go.Diagram}}
		 * @uib.description Dispatched when the user focused on the diagram
		 */
		[FOCUSED]: {},

		/**
		 * Dispatched when the user clicked on the diagram (or any object inside of it).
		 * @type {{event: go.Event, diagram: go.Diagram}}
		 * @uib.description Dispatched when the user clicked on the diagram (or any object inside of it)
		 */
		[CLICKED]: {},

		/**
		 * Dispatched when the user is dragging on the diagram (or any object inside of it).
		 * @type {{event: go.Event, diagram: go.Diagram}}
		 * @uib.description Dispatched when the user is dragging on the diagram (or any object inside of it)
		 */
		[DRAGGING]: {},
	},
});
