export default (key = null, diagram) => {
	if (!key)
		return null;

	// If it's an object (applicable to links)
	if (typeof key === 'object') {
		const fromKey = diagram.model.linkFromKeyProperty;
		const toKey = diagram.model.linkToKeyProperty;
		const links = diagram.findLinksByExample({
			[fromKey]: key.from,
			[toKey]: key.to,
		});
		return links.first();
	}

	// If it's a node
	return diagram.findNodeForKey(key);
};
