export default (item = {}, diagram, options = {}) => {
	const { data } = item;
	const fromKey = diagram.model.getFromKeyForLinkData(data);
	const toKey = diagram.model.getToKeyForLinkData(data);

	if (options.asObject)
		return {
			from: fromKey,
			to: toKey,
		};

	return fromKey + '-' + toKey;
};
