import { t } from 'sn-translate';

// Components
import '@servicenow/now-button';

/**
 * The view of the Fragment
 *
 * @param  {Object} properties
 *
 * @return {Snabbdom.Element}
 */
export default ({ diagram }) => {
	const scalePercentage = Math.floor(diagram.scale * 100);

	return (
		<div className="zoom-controls">
			<now-button
				icon-name="minus-fill"
				size="sm"
				component-name="zoom-out"
				variant="secondary"
				tooltip-content={t('Zoom Out')}
				config-aria={{ 'aria-label': t('Zoom Out') }}
			/>

			<div className="current-zoom">
				<span>{`${scalePercentage}%`}</span>
			</div>

			<now-button
				icon-name="plus-fill"
				size="sm"
				component-name="zoom-in"
				variant="secondary"
				tooltip-content={t('Zoom In')}
				config-aria={{ 'aria-label': t('Zoom In') }}
			/>
		</div>
	);
};
