import Robot from './robot';

export default (item, diagram) => {
	// Get the bounds of the item and the scale
	const bounds = item.getDocumentBounds();
	const scale = diagram.scale;

	// Calculate the width and height of the item
	const width = item.measuredBounds.width * scale;
	const height = item.measuredBounds.height * scale;

	const offset = {
		x: width / 2,
		y: height / 2,
	};

	// If it's a link
	if (item.points) {
		const firstPoint = item.points.first();
		offset.x = firstPoint.x - bounds.x;
		offset.y = firstPoint.y - bounds.y;
	}

	// Click on item
	const robot = new Robot(diagram);
	robot.mouseDown(bounds.x + offset.x, bounds.y + offset.y, 0, {});
	robot.mouseUp(bounds.x + offset.x, bounds.y + offset.y, 100, {});
};
