import {
	Shape,
	Geometry,
	PathFigure,
	PathSegment,
} from 'gojs';

Shape.defineFigureGenerator('TriangleRight', (shape, w, h) => {
	// predefined in 2.0
	return new Geometry()
		.add(new PathFigure(0, 0)
			.add(new PathSegment(PathSegment.Line, w, 0.5 * h))
			.add(new PathSegment(PathSegment.Line, 0, h).close())
		)
		.setSpots(0, 0.25, 0.5, 0.75)
		.offset(w * 0.15, 0);
});
