import {
	Shape,
	Geometry,
	PathFigure,
	PathSegment,
} from 'gojs';

Shape.defineFigureGenerator('Triangle', (shape, w, h) => {
	// predefined in 2.0
	return new Geometry()
		.add(new PathFigure(w, h)
			.add(new PathSegment(PathSegment.Line, 0, h))
			.add(new PathSegment(PathSegment.Line, 0.5 * w, h * 0.15).close())
		)
		.setSpots(0.25, 0.2, 0.75, 1)
		.offset(0, -h * 0.2);
});
