import isEqual from 'lodash/isEqual';
import pick from 'lodash/pick';
import { actionTypes } from '@servicenow/ui-core';
const {
	COMPONENT_DOM_READY,
	COMPONENT_PROPERTY_CHANGED,
	COMPONENT_RENDER_REQUESTED,
} = actionTypes;

// Constants
import {
	ZOOM_MIN,
	ZOOM_MAX,
	ZOOM_STEP,
	eventTypes,
} from './constants';

// Diagram Events
const {
	FOCUSED,
	CLICKED,
	DRAGGING,
	VIEWPORT_BOUNDS_CHANGED,

	LAYOUT_STARTED,
	LAYOUT_DONE,

	TAB_FOCUS,
	TAB_BLUR,
	TAB_CLICK,
} = eventTypes;

// Utils
import makeDiagram from './utils/make-diagram';
import setDiagramModel from './utils/set-diagram-model';
import triggerClick from './utils/trigger-click';
import getDiagramElementFromKey from './utils/get-diagram-element-from-key';

// Shapes
import './shapes/star';
import './shapes/hexagon';
import './shapes/rounded-bottom-rectangle';
import './shapes/rounded-top-rectangle';
import './shapes/triangle';
import './shapes/triangle-right';

export default {
	/**
	 * Called when the component has been connected and rendered in the DOM.
	 */
	[COMPONENT_DOM_READY]: ({ action, state, updateState, dispatch }) => {
		const { host } = action.payload;

		// Get the element
		const element = host.shadowRoot.querySelector('.gojs');

		// Make the diagram
		const {
			options,
			model,
			modelOptions,
			setTemplates,
			wcag,
		} = state.properties;
		const diagram = makeDiagram(element, {
			onFocus: (event) => {
				dispatch(FOCUSED, { event, diagram });
			},
			onClick: (event) => {
				dispatch(CLICKED, { event, diagram });
			},
			onDrag: (event) => {
				dispatch(DRAGGING, { event, diagram });
			},
			onViewportBoundsChanged: (event) => {
				// We update the state just so that it rerenders
				updateState({
					zoom: event.diagram.scale,
				});

				// Dispatch the callback function
				dispatch(VIEWPORT_BOUNDS_CHANGED, {
					event,
					diagram: event.diagram,
				});
			},
			onLayoutStarted: () => {
				// Save the start time to the diagram
				diagram.performanceStartTime = performance.now();

				// We update the state to mark the rendering started
				updateState({
					loading: true,
				});

				// Dispatch the action
				dispatch(LAYOUT_STARTED, { diagram });
			},
			onLayoutDone: (event) => {
				// Dispatch the action
				dispatch(LAYOUT_DONE, { event, diagram });
			},
			diagramOptions: {
				minScale: ZOOM_MIN,
				maxScale: ZOOM_MAX,
				...options,
			},
			wcag,
		});

		// Dispatch to set the templates
		if (typeof setTemplates === 'function')
			setTemplates({ diagram });

		// Let's make a function that updates all the bindings, but also shows
		// the preloader before it
		diagram.updateBindings = () => {
			updateState({
				updateBindingsRequested: true,
				loading: true,
			});
		};

		// Save the start time to the diagram
		diagram.performanceStartTime = performance.now();

		// Set the diagram model
		setDiagramModel(diagram, model, modelOptions);

		// Save it to the state
		updateState({
			diagram,
		});
	},

	/**
	 * Called when a property changes
	 */
	[COMPONENT_PROPERTY_CHANGED]: ({ action, state }) => {
		const {
			name,
			value,
			previousValue,
		} = action.payload;

		// In case only the reference changed
		if (isEqual(value, previousValue))
			return;

		// Get the diagram
		const {
			diagram,
			properties: { modelOptions },
		} = state;

		switch (name) {
		// If there is a model to be highlighted or cleared from highlight
		case 'options':
			console.warn('Changing the options property will not be listened to at this moment');
			break;

		// If the model nodes or edges change, then rerender the graph
		case 'model':
			setDiagramModel(diagram, value, modelOptions);
			break;
		}
	},

	/**
	 * Called before the component renders
	 */
	[COMPONENT_RENDER_REQUESTED]: ({ state, action, updateState }) => {
		const {
			updateBindingsRequested,
			diagram,
		} = state;

		if (updateBindingsRequested) {
			// Save the start time to the diagram
			diagram.performanceStartTime = performance.now();

			// Set the request to false
			updateState({
				updateBindingsRequested: false,
			});

			// Update all the target bindings, but after we show the preloader
			requestAnimationFrame(() => {
				// Save the start time to the diagram
				diagram.performanceStartTime = performance.now();

				// Update the bindings
				diagram.updateAllTargetBindings();

				// If after 100ms the state has not been updated then mark as
				// not loading (hackish, but works)
				setTimeout(() => {
					updateState({
						loading: false,
					});
				}, 100);
			});
		}

		// Check that only if loading and zoom change, then rerender
		const newState = pick(state, ['loading', 'zoom']);
		const oldState = pick(action.payload.previousRenderState, ['loading', 'zoom']);

		// If nothing changed, then no need to do anything else
		if (isEqual(oldState, newState))
			action.preventDefault();
	},


	/**
	 * Called when the zoom plus/minus buttons are pressed
	 */
	'NOW_BUTTON#CLICKED': ({ action, state, updateState }) => {
		const { diagram } = state;
		const { componentName } = action.meta;

		// Don't let it bleed
		action.stopPropagation();

		// Current zoom
		const zoom = diagram.scale;
		const zoomMin = diagram.minScale;
		const zoomMax = diagram.maxScale;

		// Calculate the new scale
		let newScale = zoom;
		switch (componentName) {
		case 'zoom-in':
			newScale = (zoom + ZOOM_STEP > zoomMax) ? zoomMax : zoom + ZOOM_STEP;
			break;
		case 'zoom-out':
			newScale = (zoom - ZOOM_STEP < zoomMin) ? zoomMin : zoom - ZOOM_STEP;
			break;
		}

		// Set the scale
		if (zoom !== newScale)
			diagram.scale = newScale;

		// We update the state just so that it rerenders
		updateState({
			zoom: diagram.scale,
		});
	},

	/**
	 * Called when the layout is done. We console.log the performance of the
	 * graph. We do it here so that we have the updated state
	 */
	[LAYOUT_DONE]: async ({ state, updateState }) => {
		// We update the state to mark the rendering that it stopped
		updateState({
			loading: false,
		});

		// Log the time
		const { diagram } = state;
		const { performanceStartTime } = diagram;
		const performanceEndTime = performance.now();
		console.info('Render duration (ms)', (performanceEndTime - performanceStartTime));
	},

	/**
	 * Called when the button gets focused
	 */
	[TAB_FOCUS]: ({ action, state }) => {
		const { key } = action.payload;
		const { diagram } = state;

		// Select the item
		const item = getDiagramElementFromKey(key, diagram);
		diagram.select(item);
	},

	/**
	 * Called when the button gets blurred
	 */
	[TAB_BLUR]: ({ state }) => {
		const { diagram } = state;
		diagram.clearSelection();
	},

	/**
	 * Called when the button gets clicked (so, to select the item)
	 */
	[TAB_CLICK]: ({ action, state }) => {
		const { diagram } = state;
		const { key } = action.payload;

		// Select the item
		const item = getDiagramElementFromKey(key, diagram);
		triggerClick(item, diagram);
	},
};
