import { t } from 'sn-translate';
import getNodeLabelFromData from './get-node-label-from-data';

export default (item = {}, diagram) => {
	const { data } = item;
	const fromKey = diagram.model.getFromKeyForLinkData(data);
	const toKey = diagram.model.getToKeyForLinkData(data);
	const fromNode = diagram.model.findNodeDataForKey(fromKey);
	const toNode = diagram.model.findNodeDataForKey(toKey);
	return t('Edge from "{0}" to "{1}"', getNodeLabelFromData(fromNode), getNodeLabelFromData(toNode));
};
