/* eslint-disable jsx-a11y/no-noninteractive-tabindex */
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';

// Diagram Events
import { eventTypes } from './constants';
const {
	TAB_FOCUS,
	TAB_BLUR,
	TAB_CLICK,
} = eventTypes;

// Utils
import mapIterator from './utils/map-iterator';
import filterIterator from './utils/filter-iterator';
import getNodeLabel from './utils/get-node-label';
import getNodeKey from './utils/get-node-key';
import getLinkKey from './utils/get-link-key';
import getLinkLabel from './utils/get-link-label';

// Fragments
import ZoomControls from './fragments/zoomControls';

// Components
import '@servicenow/now-empty-state';

/**
 * The view of the Component
 *
 * @param  {Object} state
 * @param  {Function} dispatch
 *
 * @return {Snabbdom.Element}
 */
export default (state, { dispatch }) => {
	const {
		diagram,
		loading,
		properties: {
			name,
			wcag,
			wcagNodesOrder,
		},
	} = state;

	let visibleNodes = [];
	if (wcag && diagram) {
		visibleNodes = filterIterator(diagram.nodes, item => item.isVisible());

		if (wcagNodesOrder)
			visibleNodes.sort((a, b) => wcagNodesOrder(a, b, { diagram }));
	}

	return (
		<div className="graph">
			<div className="gojs" />

			{loading
				? <div className="drawing-loader">
					<now-empty-state
						type="loading"
						widget={name || t('Graph')}
						variant="fit"
					/>
				</div>
				: null
			}

			{wcag
				? <div className="invisible-nodes">
					{visibleNodes.map((node) => {
						const links = node.findLinksOutOf();
						const nodeKey = getNodeKey(node, diagram);

						return (
							<Fragment>
								<button
									type="button"
									data-tabbable={nodeKey}
									on-focus={(event) => {
										if (event.relatedTarget)
											dispatch(TAB_FOCUS, {
												key: nodeKey,
											});
									}}
									on-blur={() => {
										dispatch(TAB_BLUR, {
											key: nodeKey,
										});
									}}
									on-click={() => {
										dispatch(TAB_CLICK, {
											key: nodeKey,
										});
									}}
								>
									{getNodeLabel(node, diagram)}
								</button>

								{mapIterator(links, (link) => {
									if (!link.isVisible())
										return null;

									const linkKey = getLinkKey(link, diagram);
									const linkKeyObj = getLinkKey(link, diagram, {
										asObject: true,
									});

									return (
										<button
											type="button"
											data-tabbable={linkKey}
											on-focus={() => {
												dispatch(TAB_FOCUS, {
													key: linkKeyObj,
												});
											}}
											on-blur={() => {
												dispatch(TAB_BLUR, {
													key: linkKeyObj,
												});
											}}
											on-click={() => {
												dispatch(TAB_CLICK, {
													key: linkKeyObj,
												});
											}}
										>
											{getLinkLabel(link, diagram)}
										</button>
									);
								})}
							</Fragment>
						);
					})}
				</div>
				: null
			}

			{diagram && !loading
				? <ZoomControls diagram={diagram} />
				: null
			}
		</div>
	);
};
