import {
	Node,
	Link,
} from 'gojs';

// Utils
import getNodeKey from './get-node-key';
import getLinkKey from './get-link-key';

export default (diagram, part, vnode) => {
	// Get key of element selected
	let key = '';
	// part is now a Node or a Group or a Link or maybe a simple Part
	if (part instanceof Node)
		key = getNodeKey(part, diagram);

	else if (part instanceof Link)
		key = getLinkKey(part, diagram);

	// If the clicked element is not a node or a key, then just return
	if (!key)
		return;

	const parent = vnode.parentNode;
	const button = (parent || vnode).querySelector(`[data-tabbable="${key}"]`);
	// If we cannot find the button, then return
	if (!button)
		return;

	button._selfTriggered = true;
	button.focus({
		preventScroll: true,
	});
};
