import {
	Diagram,
	Spot,
} from 'gojs';

// Utils
import $ from './make-graph-object';
import checkVnode from './check-vnode';
import licenseGo from './license-go';
import setTabFocusByElement from './set-tab-focus-by-element';

export default (vnode, options = {}) => {
	// Check if the vnode is correct
	checkVnode(vnode);

	// License go, to remove the watermark
	licenseGo();

	// Create the diagram
	const diagram = $(Diagram, vnode, {
		contentAlignment: Spot.Center,
		'animationManager.isEnabled': false,
		click: (event) => {
			if (options.onClick)
				options.onClick(event);
		},
		mouseDragOver: (event) => {
			// Send to the onDrag
			if (options.onDrag)
				options.onDrag(event);
		},
		...options.diagramOptions,
	});

	// When the diagram bounds changed
	diagram.addDiagramListener('ViewportBoundsChanged', (event) => {
		// Send to the onViewportBoundsChanged
		if (options.onViewportBoundsChanged)
			options.onViewportBoundsChanged(event);
	});

	// When the initial diagram layout is done
	diagram.addDiagramListener('InitialLayoutCompleted', (event) => {
		// Send to the onLayoutDone
		if (options.onLayoutDone)
			options.onLayoutDone(event);
	});

	// When the diagram layout is done
	diagram.addDiagramListener('LayoutCompleted', (event) => {
		// Send to the onLayoutDone
		if (options.onLayoutDone)
			options.onLayoutDone(event);
	});

	// When an Object gets single clicked
	// diagram.addDiagramListener('ObjectSingleClicked', (event) => {
	// 	const { part } = event.subject;
	// 	setTabFocusByElement(diagram, part, vnode);
	// });

	diagram.addDiagramListener('ChangedSelection', () => {
		// If there's at least one element selected
		if (diagram.selection.size) {
			// Add focus to first element in collection
			const part = diagram.selection.first();
			if (options.wcag)
				setTabFocusByElement(diagram, part, vnode);
		}
	});

	// When the diagram gets focused
	diagram.addDiagramListener('GainedFocus', (event) => {
		const parent = vnode.parentNode;
		const container = (parent || vnode).querySelector('.invisible-nodes');

		if (container)
			container.focus();

		if (options.onFocus)
			options.onFocus(event);
	});

	// Set the tabindex to zero
	vnode.querySelector('canvas').setAttribute('tabindex', '-1');

	return diagram;
};
